body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login-page{
    height: 100vh;
    padding-top: 40vh;
    box-sizing: border-box;
    background-image: linear-gradient(to bottom, #77c9d4, #65c7c8, #58c4b9, #53c0a6, #57bc90);
}
.name{
    text-align: center;
}
.google-login-btn{
    text-align: center;
    box-sizing: border-box;
    padding: 40px;
    width: 100%;
}
.margin {
    margin: 5px 0px 5px 5vw !important;
}

.title-top {
    text-align: center;
    margin: 20px auto;
    font-size: large;
    color: #FF473A;
}

.description {
    font-size: x-large;
    color: #000000;
    margin: 0px 0px 25px 5vw;
}

p {
    font-family: 'Montserrat';
}

.name {
    width: 50vw !important;
}

.weight {
    width: 35vw !important;
}

.line {
    margin-bottom: 20px;
    margin-top: 20px;
}

.address {
    width: 90vw !important;
}

.descriptions {
    width: 90vw !important;
    height: 20vh !important;
}

.addressee {
    text-align: center;
}
.upcoming-panel{
    margin-top: 20px;
    display: grid;
    grid-template-columns: 100px 1fr;
    padding: 20px 0 20px 0;
    font-size: 15px;
}
.panel-time{
    text-align: center;
    font-family: 'Montserrat';
}
.panel-description span{
    font-size: 16px;
    font-weight: bold;
}
.expand{
    border-top: 2px solid #58c4b9;
    margin: 20px 40px 0 40px;
    padding-top: 10px;
    text-align: center;
    grid-column-start: 1;
    grid-column-end: 3;
}
.title-top{
    text-align: center;
    margin: 20px auto;
}
#upcoming-list{
    padding: 0 40px 40px 40px;
}
.bottom-nav{
    width: 100%;
    position: fixed;
    bottom: 0;
}
.page{
    height: calc(100vh - 56px);
    box-sizing: border-box;
    width: 100vw;
    overflow-y: scroll;
    overflow-x: hidden;
}
.delivrr{
    background-image: url(data:image/png;base64,);
    height: 20px;
    width: 20px;
    background-size: contain;
    background-repeat: no-repeat;
}
